import React from "react";
import {useHistory, useParams} from "react-router-dom";
import {getMealById} from "../api";
import {Preloader} from "../components/Preloader";

export const Recipe = () => {
    const [recipe, setRecipe] = React.useState({});
    const {goBack} = useHistory();
    const {id} = useParams();

    React.useEffect(
            () => {getMealById(id).then(data => setRecipe(data.meals[0]))},
            [id]
    )

    return <React.Fragment>
        <button className='btn' onClick={goBack}>Go Back</button>
        {!recipe.idMeal
                ? <Preloader />
                : <div className='recipe'>
                    <img src={recipe.strMealThumb} alt={recipe.strMeal}/>
                    <h1>{recipe.strMeal}</h1>
                    <h6>Category: {recipe.strCategory}</h6>
                    {recipe.strArea ? <h6>Category: {recipe.strArea}</h6> : null}
                    <p>{recipe.strInstructions}</p>

                    <table className='centered'>
                      <thead>
                        <tr>
                            <th>Ingredients</th>
                            <th>Measures</th>
                      </tr>
                    </thead>

                    <tbody>
                        {Object.keys(recipe).map(key => {
                            if(key.includes('Ingredient') && recipe[key]) {
                                const keyInx = key.slice(13);
                                return <tr>
                                    <td>{recipe[key]}</td>
                                    <td>{recipe[`strMeasure${keyInx}`]}</td>
                                </tr>
                            }
                            return null
                        })}
                    </tbody>
                  </table>
                    {recipe.strYoutube
                            ? <div className='row'>
                                <h5 style={{margin: '2rem 0 1.5rem'}}>Video Recipe</h5>
                                <iframe title={id} src={`https://www.youtube.com/embed/${recipe.strYoutube.slice(-11)}`} allowFullScreen></iframe>
                            </div>
                            : null
                    }
                </div>
        }
    </React.Fragment>
}